import { 
    IContactFormLabelsProps, 
    IContactInfoProps,
    IScheduleProps,
    ContactPageLayout,
    IContactPageLayoutProps
} from "@/components/custom/contact-components";
import { loaders } from "@/data/loaders";
import { validateApiResponse } from "@/lib/error-handler";

export type TContactPageBlocks = IScheduleProps | IContactInfoProps | IContactFormLabelsProps;

export default async function ContactPage() {
    const contactPageData  = await loaders.getContactPageData();
    const data = validateApiResponse(contactPageData, "contact page");
    const {blocks} = data;

    const scheduleBlock = blocks.find(block => block.__component === "layout.schedule") as IScheduleProps;
    const contactInfoBlock = blocks.find(block => block.__component === "layout.contact-info") as IContactInfoProps;
    const contactFormBlock = blocks.find(block => block.__component === "layout.contact-form") as IContactFormLabelsProps;

    const layoutData: IContactPageLayoutProps = {
        id: data.id,
        documentId: data.documentId,
        title: data.title,
        subTitle: data.subTitle,
        description: data.description,
        scheduleData: scheduleBlock,
        contactInfoData: contactInfoBlock,
        contactFormData: contactFormBlock
    };

    return (
        <main>
            <ContactPageLayout data={layoutData} />
        </main>
    );
}